<template>
  <div> <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>面经后台</el-breadcrumb-item>
      <el-breadcrumb-item>面经管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card shadow="never" border="false">
      <template #header>
        <div class="header">
          <span>共 300 条记录</span>
          <el-button icon="el-icon-plus" size="small" type="primary" round @click="openDrawer('add')">
            添加面经
          </el-button>

        </div>

      </template>
      <el-table :data="list" stripe style="width: 100%">
        <el-table-column prop="stem" label="标题" width="400">
        </el-table-column>
        <el-table-column prop="creator" label="作者"> </el-table-column>
        <el-table-column prop="likeCount" label="点赞"> </el-table-column>
        <el-table-column prop="views" label="浏览数"> </el-table-column>
        <el-table-column prop="createdAt" label="更新时间" width="200">
        </el-table-column>

        <el-table-column label="操作" width="120px">
          <template #default="{ row }">
            <div class="actions">
              <i class="el-icon-view" @click="openDrawer('view',row.id)"></i>
              <i class="el-icon-edit-outline" @click="openDrawer('edit',row.id)"></i>
              <i class="el-icon-delete" @click="del(row.id)"></i>
            </div>
          </template>
        </el-table-column>

      </el-table>
      <!-- 抽屉 -->
      <el-drawer :visible.sync="isShowDrawer" :title="menu[type]" direction="rtl" size="60%" @closed="handleClose">
        <el-form :model="form" ref="form" label-width="80px" :rules="rules">

          <el-form-item label="标题" prop="stem">
            <el-input v-model="form.stem" placeholder="输入面经标题"></el-input>
          </el-form-item>

          <!-- 富文本编辑器 -->
          <el-form-item label="内容" prop="content">
            <quill-editor v-model="form.content"
              @blur="$refs.form.validateField('content')"></quill-editor>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="submit">确认</el-button>
            <el-button @click="handleClose">取消</el-button>
          </el-form-item>

        </el-form>
      </el-drawer>

      <!-- 页数 -->
      <el-pagination background @current-change="handleCurrentChange" :current-page="data.current"
        :page-size="data.pageSize" layout="prev, pager, next" :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import { removeArticle } from '@/api/article'
import { mapState, mapActions } from 'vuex'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
export default {
  components: {
    quillEditor
  },
  data () {
    return {
      data: {
        current: 1,
        pageSize: 10
      },
      isShowDrawer: false,
      menu: {
        add: '添加面经',
        edit: '编辑面经',
        view: '查看面经'
      },
      type: '',
      rules: {
        stem: [{ required: true, message: '请输入面经标题', trigger: 'blur' }],
        content: [{ required: true, message: '请输入面经标题', trigger: 'blur' }]
      },
      id: ''
    }
  },
  computed: {
    ...mapState('article', ['list', 'total', 'form'])
    // ...mapState('article', ['list', 'total'])
  },
  methods: {
    ...mapActions('article', ['getaList', 'createA', 'getAdetail', 'editA']),
    // 点击按钮添加文章
    async  submit () {
      // 如果是添加
      if (this.type === 'add') {
        // 校验 (对整个表单校验)
        await this.$refs.form.validate()
        this.createA(this.form)

        // 刷新页面
        this.handleCurrentChange(1)
        this.getaList(this.data)

        this.handleClose()
      } else if (this.type === 'edit') {
        // 编辑
        await this.$refs.form.validate()
        const editdata = { id: this.id, ...this.form }
        this.editA(editdata)
        this.handleClose()
        this.$message.success('编辑成功')
        this.handleCurrentChange(1)
        this.getaList(this.data)
      } else {
        // 查看
        this.handleClose()
      }
    },
    // 删除
    async del (id) {
      await removeArticle(id)
      this.$message.success('删除成功')

      if (this.list.length === 1 && this.data.current > 1) {
        this.current--
      }
      this.getaList(this.data)
    },
    // 打开抽屉
    async  openDrawer (typeVal, id) {
      // console.log(id || 'null')
      // console.log(typeVal)
      this.type = typeVal // 根据获得的typeVal，改变this.type，枚举思想,根据参数赋值，对应对象里面的属性名和值
      this.isShowDrawer = true
      // 登记id
      if (id) {
        this.id = id
      }

      // 数据回显
      if (typeVal !== 'add') {
        this.getAdetail(id)
      }
    },
    // 切换页数
    handleCurrentChange (val) {
      this.data.current = val
      this.getaList(this.data)
    },
    handleClose () {
      this.$refs.form.resetFields()
      this.isShowDrawer = false
    }
  },

  watch: {
    total (newVal) {
      return newVal
    }
  },
  created () {
    this.getaList(this.data)
    // console.log(this.form)
  }

}
</script>

<style lang="scss">
.el-card {
    margin-top: 25px;

    .header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-right: 16px;
    }

    .actions {
        font-size: 18px;
        display: flex;
        justify-content: space-around;
        color: #666;

        >i:hover {
            color: rgba(114, 124, 245, 1);
            cursor: pointer;
        }
    }
}

.el-pagination {
    margin-top: 20px;
    text-align: center;
}

.el-breadcrumb {
    margin-top: 10px;
}

.el-form {
    padding-right: 40px;
}

.quill-editor {
    ::v-deep .ql-editor {
      margin-top: 30px;
        height: 800px;
    }

}
.ql-container.ql-snow {
  width: 100%;
  /* 或者设置一个具体的宽度值，例如 500px */
  height: 200px;
  /* 默认情况下，容器的高度会随着内容自动变化 */

  margin-bottom: 30px;
}
.el-rate {
    padding: 10px 0;
}

.article-preview {
    padding: 0 40px 40px 40px;

    >h5 {
        font-size: 20px;
        color: #666;
        border-bottom: 1px dashed #ccc;
        padding-bottom: 30px;
        margin: 0 0 20px 0;
    }
}

