import Vue from 'vue'
import VueRouter from 'vue-router'

// import LayoutIndex from '@/views/layout/LayoutIndex.vue'
// import LoginIndex from '@/views/login/LoginIndex.vue'
import DashboardIndex from '@/views/dashboard/DashboardIndex.vue'
import ArticleIndex from '@/views/article/ArticleIndex.vue'
import store from '@/store'

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    { path: '/login', component: () => import('@/views/login/LoginIndex.vue') },
    {
      path: '/',
      component: () => import('@/views/layout/LayoutIndex.vue'),
      redirect: '/dashboard',
      children: [
        { path: 'dashboard', component: DashboardIndex },
        { path: 'article', component: ArticleIndex }
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  // 从store获取token
  const token = store.state.user.token
  if (token) {
    next()
  } else {
    if (to.path === '/login') {
      next()
    } else {
      next('/login')
    }
  }
})

export default router
