import { getArticleList, createArticle, getArticleDetail, updateArticle } from '@/api/article'
import { Message } from 'element-ui'
// import router from '@/router'

const state = () => {
  return {
    list: [],
    total: 0,
    // 富文本编辑器内容
    form: {
      stem: '', // 标题
      content: ''// 内容
    }
  }
}
const mutations = {
  setList (state, newList) {
    state.list = newList
  },
  setTotal (state, newTotal) {
    state.total = newTotal
  },

  // 设置标题
  setStem (state, newStem) {
    state.form.stem = newStem
  },
  // 设置内容
  setContent (state, newContent) {
    state.form.content = newContent
  }

}
const actions = {

  // 获取文章列表
  async getaList (cxt, data) {
    const res = await getArticleList(data)
    console.log(res)
    cxt.commit('setList', res.data.rows)
    cxt.commit('setTotal', res.data.total)
  },

  // 添加文章
  async createA (cxt, data) {
    try {
      const res = await createArticle(data)
      // console.log(res.data.id) // 新文章id
      Message.success('添加成功')
      return res.data.id
    } catch (err) {
      if (err.response) {
        Message.error(err.response.data.message)
      }
    }
  },

  // 获取文章详情
  async getAdetail (cxt, id) {
    const res = await getArticleDetail(id)
    // console.log(res.data)
    cxt.commit('setStem', res.data.stem)
    cxt.commit('setContent', res.data.content)
  },

  // 编辑文章
  async editA (cxt, data) {
    const res = await updateArticle(data)
    // console.log(res)
    return res
  }

}
const getters = {}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
